import React, { useRef, useState, useEffect } from "react";
import styled from "styled-components";
import socket from "../../socket";
import { useAuth } from "../../contexts/AuthContext";

import {
  Button,
  Form,
  Input,
  theme,
  Typography,
  Divider,
  Card,
  Avatar,
} from "antd";

const { useToken } = theme;
const { Text, Title } = Typography;

const Main = (props) => {
  const [err, setErr] = useState(false);
  const [errMsg, setErrMsg] = useState("");
  const { user, logout } = useAuth();
  const userRef = useRef(user?.name || null);
  const roomRef = useRef();
  const { token } = useToken();

  useEffect(() => {
    socket.on("FE-error-user-exist", ({ error }) => {
      if (!error) {
        const roomName = roomRef.current.value;

        props.history.push(`/room/${roomName}`);
      } else {
        setErr(error);
        setErrMsg("User name already exist");
      }
    });
  }, [props.history]);

  function clickJoin() {
    const roomName = roomRef.current.input.value;
    const userName = userRef.current.input.value;

    if (!roomName || !userName) {
      setErr(true);
      setErrMsg("Enter Room Name or User Name");
    } else {
      socket.emit("BE-check-user", { roomId: roomName, userName });
    }
  }

  return (
    <PageContainer>
      <MainCard>
        <UserCard>
          <Avatar size={48} style={{ background: token.colorPrimary }}>
            {user.name.charAt(0).toUpperCase()}
          </Avatar>
          <UserInfoContainer>
            <Title level={4} style={{ margin: 0 }}>
              {user.name}
            </Title>
            <Text type="secondary">{user.email}</Text>
            <Text type="secondary" style={{ fontSize: token.fontSizeSM }}>
              Member since {new Date(user.created_at).toLocaleDateString()}
            </Text>
          </UserInfoContainer>
          <Button
            type="primary"
            style={{
              boxShadow: "none",
            }}
            danger
            onClick={logout}
          >
            Logout
          </Button>
        </UserCard>

        <Divider />

        <Title
          level={3}
          style={{ textAlign: "center", color: token.colorTextBase }}
        >
          Start or Join Meeting
        </Title>

        <Form layout="vertical">
          <Form.Item
            label="Room Name"
            validateStatus={err ? "error" : ""}
            help={err && errMsg}
          >
            <Input
              ref={roomRef}
              placeholder="Enter room name..."
              size="large"
            />
          </Form.Item>

          <Form.Item label="Display Name">
            <Input
              ref={userRef}
              placeholder="Enter display name..."
              defaultValue={user.name}
              size="large"
            />
          </Form.Item>

          <Form.Item>
            <Button
              type="primary"
              size="large"
              block
              onClick={clickJoin}
              style={{
                height: "40px",
                background: token.colorPrimary,
              }}
            >
              Join Meeting
            </Button>
          </Form.Item>
        </Form>
      </MainCard>
    </PageContainer>
  );
};

const PageContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 20px;
`;

const MainCard = styled(Card)`
  width: 100%;
  max-width: 500px;
  border-radius: ${() => {
    const { token } = theme.useToken();
    return token.borderRadiusLG;
  }}px;
  box-shadow: ${() => {
    const { token } = theme.useToken();
    return token.boxShadow;
  }};
`;

const UserCard = styled.div`
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 16px;
  background: ${() => {
    const { token } = theme.useToken();
    return token.colorBgContainer;
  }};
  border-radius: ${() => {
    const { token } = theme.useToken();
    return token.borderRadius;
  }}px;
`;

const UserInfoContainer = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 4px;
`;

export default Main;
